<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	id: "vue",
	setup: function () {
		vue.start ()
		return vue.export ()
		},
	watch: {
		$route () {
			vue.parse_url ()
			vue.then (function () {
				if (vue.ready.value) vue.emit ("route")
				else vue.watch (vue.ready, function (ready) { if (ready) vue.emit ("route") })
				})
			vue.catch (function () {
				vue.emit ("route")
				})
			},
		},
	method: {
		loading: function () {
			if (vue.on.loading.spin ()) return "on"
			else return "off"
			},
		},
	})
</script>

<template>
	<media-player:audio v-if="vue.setting ('media-player:audio')"/>
	<media-query:line v-if="vue.setting ('media-query:line')"/>
	<media-query:screen v-if="vue.setting ('media-query:screen')"/>
	<div id="media-query:loading" v-bind:aria-data="loading ()">
{{vue.on.loading.data}}
</div>
	<router-view v-if="vue.system.setup" id="application"/>
</template>
