<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ["component"] ({
	id: "media-query:line",
	attribute: [],
	setup: function () {
		var line = vue.reference ("on")
		vue.watch (vue.on.line, function (on) {
			if (on) line.value = "on"
			else line.value = "off"
			})
		return {vue, line}
		},
	})
</script>

<template id="media-query:line" type="component">
	<div v-if="line" id="media-query:line" v-bind:aria-data="line">
		<div id="media-query:line/section">
			on:line
		</div>
	</div>
	<div id="media-query:line" v-bind:aria-data="line" v-else>
		<div id="media-query:line/section">
			off:line
		</div>
	</div>
</template>
