vue.model = function (model, ... value) {
	if (value.length) return vue.model.data [vue.model.format (model)] = value [zero]
	else if (model = vue.model.data [vue.model.format (model)]) return model
	else return ""
	}

vue.model.format = function (model) {
	var prefix = ("vue.model") + (".")
	if ((model = lib.to.string (model) || "*").begin (prefix)) return model.shift (prefix)
	return model
	}

vue.model.delete = function (model) {
	return delete vue.model.data [vue.model.format (model)]
	}

vue.model.clear = function (model) {
	if (model) {
		if (lib.is.array (model)) for (var i in model) vue.model.delete (model [i])
		else vude.model.delete (model)
		}
	else for (var i in vue.model.data) delete vue.model.data [i]
	}

vue.model.data = vue.reactive ()

