lib.prop = function (name) {
	var prop = []
	for (var i in (name = name.split ("-"))) {
		if (i == zero) prop.push (name [i])
		else prop.push (name [i].uc_begin ())
		}
	return prop.implode ()
	}

lib.prop.s = function (... name) {
	return name.join ("-")
	}

