vue.commit = function (context) { return vue.x.commit ("mutation", context) }
vue.dispatch = function (context) { return vue.x.dispatch ("action", context) }
vue.method = function (key, value) { return vue.method [key] = value }
vue.config = function (key) { if (ini.config) return ini.config [key] }

vue.then = function (context) { return new IO (context) }
vue.catch = function (context) { if (vue.config ("web:browser")) context.call () }

class IO {
	constrcutor (context) { if (vue.config ("web:socket")) context.call () }
	catch (context) { context.call () }
	}

